<template>
    <div class="wrap">
        <section class="archive">
            <ul>
                <li class="item"
                    v-for="year in years">
                    <p>{{year}}</p>
                    <ul>
                        <li v-for="article in archive[year]">
                            <span class="date">
                                {{article.date | dateParse}}
                            </span>
                            <span class="title"
                                  @click="detail(article._id)">
                                {{article.title}}
                            </span>
                        </li>
                    </ul>
                </li>
            </ul>
        </section>
    </div>
</template>
<script>
    import api from '../config'
    export default{
        data(){
            return {
                articles: null,
                years:[1],
            }
        },
        filters: {
            dateParse: value=> {
                let d = new Date(value)
                return d.getFullYear() + '年' +
                        (d.getMonth() + 1) + '月' +
                        d.getDate() + '日'
            }
        },
        created(){
            this.$http.get(api.url+'/articleList')
                    .then((response)=> {
                        let arcs = response.body
                        this.articles = arcs.sort((i, j)=> {
                            return new Date(j.date) - new Date(i.date)
                        })
                    }, (response)=> {
                        console.log(response)
                    })
        },
        ready(){

        },
        methods: {
            detail(id){
                this.$router.go('/article?id=' + id)
            }
        },
        computed: {
            archive(){
                if (!this.articles) {
                    return {1:[{title:'',date:''}]}
                }
                let archive = {}
                this.years.splice(0,1)
                this.articles.forEach(atc=> {
                    let year = new Date(atc.date).getFullYear()
                    if (!archive[year]) {
                        this.years.push(year)
                        archive[year] = []
                    }
                    archive[year].push(atc)
                })
                return archive
            },
        },

    }
</script>


